export const validator = (f, state, setState, ...names) => {
    return function () {
        const err = names[0] + "Error";
        names = names.map(name => state[name]);
        const check = f(...names);
        if (!check[0]) {
            setState(err, check[1]);
            return false;
        }
        else setState(err, "");
        return true;
    }
}

export const checkPassword = (password) => {
    if (password.length < 8 || password.length > 64) {
        return [false, "   8  64 "];
    }
    else if (!password.match(/^[a-zA-Z0-9\-_!@#$%^&?*+=(){}[\]<>~]+$/)) {
        return [false, " "];
    }
    else if (!password.match(/[\-_!@#$%^&?*+=(){}[\]<>~]/)) {
        return [false, "   "]
    }
    else if (!password.match(/[0-9]/)) {
        return [false, "   "]
    }
    else if (!password.match(/[A-Z]/)) {
        return [false, "     "]
    }
    return [true];
}

export const checkPasswordRepeat = (password, passwordRepeat) => {
    if (passwordRepeat !== password) {
        return [false, "  "];
    }
    return [true];
}

export const checkEmail = (email) => {
    if (!email.match(/^([a-zA-Z0-9_\-\.]+)@([a-zA-Z0-9_\-\.]+)\.([a-zA-Z]{2,5})$/)) {
        return [false, " email"];
    }
    return [true];
}

export const checkFullName = (fullName) => {
    if (!fullName.length || !fullName.match(/^([A-Z-][a-z-]+[\s]?){2,3}$/)) {
        return [false, "    2-3 ,        , , "];
    }
    else if (fullName.length > 64) {
        return [false, "     64 "]
    }
    return [true];
}

export const checkEmpty = (value) => {
    if (!value) return [false, " "];
    return [true];
}